"use client";

import { useState } from "react";
import Navbar from "@/app/components/Navbar";
import useTransactions from "@/hooks/useTransactions";
import { useUser } from "@/context/userContext";

export default function AddTransaction() {
  const { createTransaction, mutations } = useTransactions();
  const { createTransaction: createTransactionMutation } = mutations;
  const { user } = useUser();

  const [formData, setFormData] = useState({
    amount: "",
    transaction_type: "expense",
    date: new Date().toISOString().split("T")[0],
    description: "",
    category_id: "",
    wallet_id: "", // You'll need to get this from user context or props
    budget_id: null,
    goal_id: null,
  });

  const [categories, setCategories] = useState([
    { id: "groceries", name: "Groceries" },
    { id: "utilities", name: "Utilities" },
    { id: "entertainment", name: "Entertainment" },
    { id: "transport", name: "Transport" },
    { id: "dining", name: "Dining" },
    { id: "shopping", name: "Shopping" },
    { id: "healthcare", name: "Healthcare" },
    { id: "education", name: "Education" },
  ]);

  const [categorizeAs, setCategorizeAs] = useState("category");
  const [showSuccess, setShowSuccess] = useState(false);

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // Handle transaction type toggle
  const handleTransactionTypeChange = (type) => {
    setFormData((prev) => ({
      ...prev,
      transaction_type: type,
    }));
  };

  // Handle categorize as change
  const handleCategorizeAsChange = (e) => {
    const value = e.target.value.toLowerCase();
    setCategorizeAs(value);

    // Reset related fields when categorization type changes
    setFormData((prev) => ({
      ...prev,
      category_id: value === "category" ? "" : null,
      budget_id: value === "budget" ? "" : null,
      goal_id: value === "goal" ? "" : null,
    }));
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!user?.id) {
      console.error("User not found. Please make sure you're logged in.");
      return;
    }

    try {
      // Prepare the data for submission with user_id
      const submissionData = {
        ...formData,
        user_id: user.id, // Add the user_id here
        amount: parseFloat(formData.amount),
        // Only include the relevant ID based on categorization type
        ...(categorizeAs === "budget" && {
          budget_id: formData.budget_id,
          category_id: null,
          goal_id: null,
        }),
        ...(categorizeAs === "goal" && {
          goal_id: formData.goal_id,
          category_id: null,
          budget_id: null,
        }),
        ...(categorizeAs === "category" && {
          category_id: formData.category_id,
          budget_id: null,
          goal_id: null,
        }),
      };

      await createTransaction(submissionData);

      // Show success message
      setShowSuccess(true);

      // Reset form
      setFormData({
        amount: "",
        transaction_type: "expense",
        date: new Date().toISOString().split("T")[0],
        description: "",
        category_id: "",
        wallet_id: "",
        budget_id: null,
        goal_id: null,
      });

      // Hide success message after 3 seconds
      setTimeout(() => setShowSuccess(false), 3000);
    } catch (err) {
      console.error("Failed to create transaction:", err);
    }
  };

  // Handle cancel
  const handleCancel = () => {
    setFormData({
      amount: "",
      transaction_type: "expense",
      date: new Date().toISOString().split("T")[0],
      description: "",
      category_id: "",
      wallet_id: "",
      budget_id: null,
      goal_id: null,
    });
    setCategorizeAs("category");
  };

  // Get dynamic field label based on categorization type
  const getDynamicFieldLabel = () => {
    switch (categorizeAs) {
      case "budget":
        return "Select Budget";
      case "goal":
        return "Select Goal";
      case "category":
      default:
        return "Select Category";
    }
  };

  // Get dynamic options based on categorization type
  const getDynamicOptions = () => {
    switch (categorizeAs) {
      case "budget":
        return [
          { id: "budget1", name: "Monthly Expenses" },
          { id: "budget2", name: "Entertainment Budget" },
          { id: "budget3", name: "Food Budget" },
        ];
      case "goal":
        return [
          { id: "goal1", name: "New Laptop" },
          { id: "goal2", name: "Vacation Fund" },
          { id: "goal3", name: "Emergency Fund" },
        ];
      case "category":
      default:
        return categories;
    }
  };

  const loading = createTransactionMutation.isPending;
  const error = createTransactionMutation.error;

  return (
    <div className="min-h-screen bg-[#21121e] text-white font-sans">
      <Navbar />

      {/* Main Content */}
      <main className="flex flex-col items-center py-8 px-4 sm:px-6 lg:px-8">
        <div className="mt-15 w-full max-w-3xl">
          {/* Page Header */}
          <div className="flex flex-wrap justify-between gap-3 p-4 mb-6">
            <div className="flex w-full flex-col gap-3 text-center">
              <h1 className="text-white text-3xl sm:text-4xl font-black leading-tight tracking-[-0.033em]">
                Add a New Transaction
              </h1>
              <p className="text-accent-cream/70 text-base font-normal leading-normal">
                Enter the details below to record your transaction.
              </p>
            </div>
          </div>

          {/* Success Message */}
          {showSuccess && (
            <div className="mb-6 p-4 bg-green-500/20 border border-green-500 rounded-lg text-green-300 text-center">
              Transaction created successfully!
            </div>
          )}

          {/* Error Message */}
          {error && (
            <div className="mb-6 p-4 bg-red-500/20 border border-red-500 rounded-lg text-red-300 text-center">
              Error: {error.message}
            </div>
          )}

          {/* Transaction Form */}
          <form
            onSubmit={handleSubmit}
            className="bg-white/10 p-6 sm:p-8 rounded-xl flex flex-col gap-6"
          >
            {/* Transaction Type Toggle */}
            <div className="flex justify-center">
              <div className="flex items-center bg-black/30 p-1 rounded-full">
                <button
                  type="button"
                  className={`px-4 sm:px-6 py-2 text-sm font-bold rounded-full transition-colors cursor-pointer ${
                    formData.transaction_type === "expense"
                      ? "bg-[#9c167f] text-white"
                      : "text-accent-cream/70 hover:text-accent-cream"
                  }`}
                  onClick={() => handleTransactionTypeChange("expense")}
                >
                  Expense
                </button>
                <button
                  type="button"
                  className={`px-4 sm:px-6 py-2 text-sm font-medium rounded-full transition-colors cursor-pointer ${
                    formData.transaction_type === "income"
                      ? "bg-[#9c167f] text-white"
                      : "text-accent-cream/70 hover:text-accent-cream"
                  }`}
                  onClick={() => handleTransactionTypeChange("income")}
                >
                  Income
                </button>
              </div>
            </div>

            {/* Amount and Date Fields */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 sm:gap-6">
              {/* Amount Input */}
              <label className="flex flex-col">
                <span className="text-white text-base font-medium leading-normal pb-2">
                  Amount
                </span>
                <input
                  name="amount"
                  value={formData.amount}
                  onChange={handleInputChange}
                  className="w-full rounded-lg text-white focus:outline-none focus:ring-2 border border-transparent bg-black/20 focus:border-black h-12 sm:h-14 placeholder:text-white/30 px-4 text-base font-normal leading-normal"
                  placeholder="â‚±0.00"
                  type="number"
                  step="0.01"
                  min="0"
                  required
                />
              </label>

              {/* Date Input */}
              <label className="flex flex-col">
                <span className="text-white text-base font-medium leading-normal pb-2">
                  Date
                </span>
                <div className="flex w-full items-stretch rounded-lg">
                  <input
                    name="date"
                    value={formData.date}
                    onChange={handleInputChange}
                    className="flex-1 rounded-l-lg text-white focus:outline-none focus:ring-2 border border-transparent bg-black/20 focus:border-black h-12 sm:h-14 placeholder:text-white/30 px-4 border-r-0 text-base font-normal leading-normal"
                    type="date"
                    required
                  />
                  <div className="text-white flex border border-transparent bg-black/20 items-center justify-center px-3 sm:px-4 rounded-r-lg border-l-0">
                    <svg
                      className="w-5 h-5 sm:w-6 sm:h-6"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"
                      />
                    </svg>
                  </div>
                </div>
              </label>
            </div>

            {/* Description Field */}
            <div className="flex flex-col">
              <span className="text-white text-base font-medium leading-normal pb-2">
                Description
              </span>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                className="w-full rounded-lg text-white focus:outline-none focus:ring-2 border border-transparent bg-black/20 focus:border-black/20 min-h-24 placeholder:text-white/30 p-4 text-base font-normal leading-normal resize-none"
                placeholder="e.g., Morning Coffee"
                rows={4}
                required
              />
            </div>

            {/* Categorization Fields */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 sm:gap-6">
              {/* Categorize As */}
              <label className="flex flex-col">
                <span className="text-white text-base font-medium leading-normal pb-2">
                  Categorize As
                </span>
                <div className="relative w-full">
                  <select
                    value={categorizeAs}
                    onChange={handleCategorizeAsChange}
                    className="appearance-none w-full cursor-pointer rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-primary/50 border border-transparent bg-black/20 focus:border-black/20 h-12 sm:h-14 px-4 text-base font-normal leading-normal"
                  >
                    <option value="budget">Budget</option>
                    <option value="goal">Goal</option>
                    <option value="category">Category</option>
                  </select>
                  <div className="pointer-events-none absolute right-3 top-1/2 -translate-y-1/2 text-white">
                    <svg
                      className="w-5 h-5"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 9l-7 7-7-7"
                      />
                    </svg>
                  </div>
                </div>
              </label>

              {/* Dynamic Selection Field */}
              <label className="flex flex-col">
                <span className="text-white text-base font-medium leading-normal pb-2">
                  {getDynamicFieldLabel()}
                </span>
                <div className="relative w-full">
                  <select
                    name={
                      categorizeAs === "budget"
                        ? "budget_id"
                        : categorizeAs === "goal"
                        ? "goal_id"
                        : "category_id"
                    }
                    value={
                      formData[
                        categorizeAs === "budget"
                          ? "budget_id"
                          : categorizeAs === "goal"
                          ? "goal_id"
                          : "category_id"
                      ] || ""
                    }
                    onChange={handleInputChange}
                    className="appearance-none w-full cursor-pointer rounded-lg text-white focus:outline-none focus:ring-2 border border-transparent bg-black/20 focus:border-black/20 h-12 sm:h-14 px-4 text-base font-normal leading-normal"
                    required
                  >
                    <option value="">
                      Select{" "}
                      {categorizeAs.charAt(0).toUpperCase() +
                        categorizeAs.slice(1)}
                    </option>
                    {getDynamicOptions().map((option) => (
                      <option key={option.id} value={option.id}>
                        {option.name}
                      </option>
                    ))}
                  </select>
                  <div className="pointer-events-none absolute right-3 top-1/2 -translate-y-1/2 text-white">
                    <svg
                      className="w-5 h-5"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 9l-7 7-7-7"
                      />
                    </svg>
                  </div>
                </div>
              </label>
            </div>

            {/* Action Buttons */}
            <div className="flex flex-col sm:flex-row items-center justify-center gap-3 sm:gap-4 pt-4">
              <button
                type="button"
                onClick={handleCancel}
                disabled={loading}
                className="w-full sm:w-auto flex items-center justify-center rounded-full h-12 bg-black/10 text-accent-cream text-sm font-bold leading-normal tracking-[0.015em] px-6 sm:px-8 border-2 border-[#9c167f] hover:bg-[#9c167f] transition-colors cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={loading}
                className="w-full sm:w-auto flex items-center justify-center rounded-full h-12 bg-[#9c167f] text-white text-sm font-bold leading-normal tracking-[0.015em] px-6 sm:px-8 hover:bg-[#9c167f]/90 transition-colors cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? "Saving..." : "Save Transaction"}
              </button>
            </div>
          </form>
        </div>
      </main>
    </div>
  );
}
